import { useIsMobile } from 'hooks/use-is-mobile';
import { isAndroid } from 'utils/is-android';
import { GooglePlay } from './GooglePlay';
import { Ios } from './Ios';
import './styles.scss';

export const ClientLink = () => {
  const isMobile = useIsMobile();
  if (isMobile) {
    return isAndroid() ? (
      <a href="https://wearesho.com/#apps-android">
        <GooglePlay />
      </a>
    ) : (
      <a href="https://wearesho.com/#apps-ios">
        <Ios />
      </a>
    );
  }
  return (
    <div className="whitespace-nowrap">
      <a href="https://wearesho.com/#apps-android" className="inline-block">
        <GooglePlay />
      </a>
      <a href="https://wearesho.com/#apps-ios" className="inline-block ml-4">
        <Ios />
      </a>
    </div>
  );
};
