import React, { useRef, useState, useEffect } from "react";
import { AccordionDataType } from "types";
import { Arrow } from "./Arrow";
import styles from "./styles.module.scss";

type ItemType = {
  data: AccordionDataType;
  isOpen: boolean;
  onClick: () => void;
};

export const Item: React.FC<ItemType> = ({ data, isOpen, onClick }) => {
  const ref = useRef<HTMLDivElement>(null);
  const [height, setHeight] = useState(0);

  useEffect(() => {
    const content = ref.current;

    if (!content) {
      return;
    }

    if (isOpen) {
      setHeight(content.scrollHeight);
    } else {
      setHeight(0);
    }
  }, [isOpen]);

  return (
    <li className={`${styles.accordionItem} ${isOpen ? styles.active : ""}`}>
      <h4 className={styles.accordionItemTitle}>
        <button className={styles.accordionItemBtn} onClick={onClick}>
          {data.title}
          <Arrow />
        </button>
      </h4>
      <div className={styles.accordionItemContainer} style={{ height }}>
        <div ref={ref} className={styles.accordionItemContent}>
          {data.content}
        </div>
      </div>
    </li>
  );
};
